import React from 'react';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import './Header.css';
const Header = props =>(

    <header className="header">
      <nav className="header_navi">
          <div className='header-toggle'>
            <DrawerToggleButton click={props.drawerClickHandler}/>
          </div>
          <div className="header_logo"><a href="/monk-windwalker-guide/#/">MonkWindwalkerGuide</a></div>
          <div className="spacer"></div>
          <div className="header_item">
            <ul>
              <li><a href="/monk-windwalker-guide/#/about">About me</a></li>
              <li><a href="/">FAQ</a></li>
              <li><a href="/">Talents</a></li>
              <li><a href="/">Ability Priority</a></li>
              <li><a href="/">Best-in-Slot</a></li>
              <li><a href="/">Azerite</a></li>
            </ul>
          </div>
      </nav>
    </header>
  );

export default Header;
